let i = 0
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)//å­—ç¬¦ä¸²è½¬åŒ–æˆå¯¹è±¡
//logoæœ‰å›¾ç‰‡å’Œæ–‡æœ¬
let $last = $('.last')
let hasMap = xObject === [] || [
    {logo: 'A', url: 'https://www.acfun.cn/'},
    {logo: 'B', url: 'https://www.baidu.com/'},
    {logo: 'C', url: 'https://www.csdn.com/'},
]
//æ·»åŠ å†…å®¹æ˜¯$('æ ‡ç­¾'),ç„¶åŽä½¿ç”¨append appendTo appendAfter
//å®šä¹‰ä¸€ä¸ªå‡½æ•°
const simpliUrl = (url) => {
    return url.replace(/((http:\/\/)|(https:\/\/))(www\.)?/, '').replace('/', '')
}

const render = () => {
    $('.itemList').find('.item').remove()
    hasMap.forEach((node, index) => {
        let $li = $(`
        <li class="item">
        <div class="logo">${node.logo[0]}</div>
        <div class="content">${simpliUrl(node.url)}</div>
        <div class="delete">
        <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-shanchu"></use>
        </svg>
        </div>
      </li>`).insertBefore($last)
        $li.on('click', () => {
            window.open(node.url)
        })
        $('.item').on('click', '.delete', e => {
            e.stopPropagation()//é˜»æ­¢å†’æ³¡
            hasMap.splice(index, 1)
            render()
        })
    })
}
render()

$('.last').on('click', () => {
    let url = window.prompt('ä½ è¦æ·»åŠ çš„ç½‘ç«™')
    console.log(url)
    if (url === '' || url.indexOf('http') !== 0) {
        url = simpliUrl('https://' + url)

    }
    console.log(url)
    hasMap.push({logo: url[0], url: url})
    render()

    //åœ¨ä½¿ç”¨localstrageä¸­å‚¨å­˜æ•°æ®
    // console.log($('.container'))
})

//ç›‘å¬é¡µé¢ç¦»å¼€
window.onbeforeunload = () => {
    //å°†æ•°æ®å‚¨å­˜åœ¨localstrageä¸­ï¼Œåªèƒ½å­˜å­—ç¬¦ä¸²
    const string = JSON.stringify(hasMap)//åºåˆ—åŒ–
    localStorage.setItem('x', string)


}
$(document).on('keypress',(e)=>{
    const {key} =e
    for (let i =0;i<hasMap.length;i++){
        if(hasMap[i].logo.toLowerCase() ===key){
            window.open(hasMap[i].url)
        }
    }
})
